<template>
  <div class="vux-uploader">
    <div class="vux-uploader_hd">
      <p class="vux-uploader_title">{{ title }}</p>
      <div class="vux-uploader_info">{{ fileList.length }} / {{ limit }}</div>
    </div>
    <div class="vux-uploader_bd">
      <ul class="vux-uploader_files">
        <li
          :class="{
              'vux-uploader_file': true,
              'vux-uploader_file-status': !!item.fetchStatus && item.fetchStatus !== 'success'
            }"
          v-for="(item, index) in fileList"
          :key="index"
          :style="{
              backgroundImage: `url(${item.url})`
            }"
          @click="handleFileClick($event, item, index)"
        >
          <div
            v-if="!!item.fetchStatus && item.fetchStatus !== 'success'"
            class="vux-uploader_file-content"
          >
            {{ item.fetchStatus === 'progress' ? item.progress + '%' : '' }}
            <!-- progress !== 0 && progress < 100 -->
            <i
              v-if="item.fetchStatus === 'fail'"
              class="upload-error"
            ></i>
          </div>
        </li>
      </ul>
      <div
        class="vux-uploader_input-box"
        v-show="fileList.length < limit && !readonly"
      >
        <input
          class="vux-uploader_input"
          ref="input"
          type="file"
          name="uploadInput"
          accept="image/*"
          :capture="capture"
          :multiple="multiple"
          @change="change"
        />
      </div>
    </div>
    <priviewer
      ref="previewer"
      :list="fileList | filterList"
      :readonly="readonly"
      @on-delete="deleteImg"
    ></priviewer>
  </div>
</template>
<script>
import Priviewer from './previewer'
import { handleFile, transformCoordinate, dataURItoBlob } from '../utils'

// compatibility for window.URL
const URL =
  window.URL && window.URL.createObjectURL
    ? window.URL
    : window.webkitURL && window.webkitURL.createObjectURL
    ? window.webkitURL
    : null

export default {
  name: 'Uploader',
  components: {
    Priviewer,
  },
  model: {
    prop: 'files',
    event: 'on-fileList-change',
  },
  props: {
    title: {
      type: String,
      default: '图片上传',
    },
    files: {
      type: Array,
      default: () => [],
    },
    limit: {
      type: Number | String,
      default: 5,
    },
    limitPrompt: {
      type: Function,
      default: (limit) => `不能上传超过${limit}张图片`,
    },
    capture: {
      type: Boolean | String,
      default: false,
    },
    enableCompress: {
      type: Boolean,
      default: true,
    },
    maxWidth: {
      type: String | Number,
      default: 1024,
    },
    quality: {
      type: String | Number,
      default: 0.92,
    },
    url: {
      type: String,
    },
    headers: {
      type: Object,
      default: () => {},
    },
    withCredentials: {
      type: Boolean,
      default: false,
    },
    params: {
      type: Object,
    },
    name: {
      type: String,
      default: 'file',
    },
    autoUpload: {
      type: Boolean,
      default: true,
    },
    multiple: {
      type: String | Boolean,
      default: '',
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      fileList: this.files,
      currentIndex: 0,
      previewerIndex: 0,
    }
  },
  filters: {
    filterList(list = []) {
      return list.map((item) => {
        const { url } = item
        return {
          src: url,
          msrc: url,
          w: 0,
          h: 0,
        }
      })
    },
  },
  watch: {
    files: {
      deep: true,
      handler(files) {
        this.fileList = files
      },
    },
    fileList: {
      deep: true,
      handler(fileList) {
        this.$emit('on-fileList-change', fileList)
      },
    },
  },
  methods: {
    async change(e) {
      const {
        enableCompress,
        maxWidth,
        quality,
        limit,
        limitPrompt,
        fileList,
        autoUpload,
        uploadFile,
      } = this
      const target = e.target || e.srcElement
      const inputChangeFiles = target.files
      if (inputChangeFiles.length > 0) {
        if (fileList.length + inputChangeFiles.length > limit) {
          alert(limitPrompt(limit))
          return
        }
        Promise.all(
          Array.prototype.map.call(inputChangeFiles, (file) => {
            const doSquash = file.type === 'image/jpeg'
            return handleFile(
              file,
              {
                maxWidth,
                quality,
                enableCompress,
              },
              doSquash
            ).then((blob) => {
              const blobURL = URL.createObjectURL(blob)
              const fileItem = {
                url: blobURL,
                blob,
              }
              for (let key in file) {
                if (['slice', 'webkitRelativePath'].indexOf(key) === -1) {
                  fileItem[key] = file[key]
                }
              }
              if (autoUpload) {
                uploadFile(blob, fileItem)
                  .then((result) => {
                    fileList.push(fileItem)
                    this.$emit('on-change', fileItem, fileList)
                  })
                  .catch((e) => {
                    fileList.push(fileItem)
                  })
              } else {
                fileList.push(fileItem)
                this.$emit('on-change', fileItem, fileList)
              }
            })
          })
        ).then(() => {
          this.$refs.input.value = ''
        })
      } else {
        this.$emit('on-cancel')
      }
    },
    handleFileClick(e, item, index) {
      this.$refs.previewer.show(index)
      this.previewerIndex = index
    },
    deleteImg(currentIndex) {
      const { fileList } = this
      const delFn = () => {
        const deleteItem = fileList[currentIndex]
        fileList.splice(currentIndex, 1)
        this.$nextTick(() => {
          this.$emit('on-change', deleteItem, fileList)
          this.$refs.previewer.close()
        })
      }
      if (this.$listeners['on-delete']) {
        this.$emit('on-delete', fileList[currentIndex], delFn)
      } else {
        delFn()
      }
    },
    uploadFile(blob, fileItem) {
      return new Promise((resolve, reject) => {
        const me = this
        const { url, params, name, headers, withCredentials } = me
        me.$set(fileItem, 'fetchStatus', 'progress')
        me.$set(fileItem, 'progress', 0)
        const formData = new FormData()
        const xhr = new XMLHttpRequest()
        formData.append(name, blob)
        if (params) {
          for (let key in params) {
            formData.append(key, params[key])
          }
        }
        xhr.onreadystatechange = () => {
          if (xhr.readyState === 4) {
            if (xhr.status === 200) {
              const result = JSON.parse(xhr.responseText)
              me.$emit('on-success', result, fileItem)
              me.$set(fileItem, 'fetchStatus', 'success')
              resolve(result)
            } else {
              me.$emit('on-error', xhr)
              me.$set(fileItem, 'fetchStatus', 'fail')
              reject(xhr)
            }
          }
        }
        xhr.upload.addEventListener(
          'progress',
          function (evt) {
            if (evt.lengthComputable) {
              const precent = Math.ceil((evt.loaded / evt.total) * 100)
              me.$set(fileItem, 'progress', precent)
            }
          },
          false
        )

        xhr.open('POST', url, true)

        for (let key in headers) {
          if (headers.hasOwnProperty(key) && headers[key] !== null) {
            xhr.setRequestHeader(key, headers[key])
          }
        }

        if (withCredentials && 'withCredentials' in xhr) {
          xhr.withCredentials = true
        }

        xhr.send(formData)
      })
    },
  },
}
</script>
<style lang="less">
@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: 'weui';
  src: url('data:application/octet-stream;base64,')
    format('truetype');
}

.vux-uploader {
  padding: 10px 15px;
  .vux-uploader_hd {
    display: flex;
    padding-bottom: 10px;
    .vux-uploader_title {
      flex: 1;
    }
    .vux-uploader_info {
      color: #b2b2b2;
    }
  }
  .vux-uploader_bd {
    overflow: hidden;
    margin-left: -9px;
    .vux-uploader_files {
      list-style: none;
      .vux-uploader_file {
        float: left;
        margin-left: 9px;
        margin-bottom: 9px;
        width: 79px;
        height: 79px;
        background: no-repeat center center;
        background-size: cover;
      }
      .vux-uploader_file-status {
        position: relative;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background: rgba(0, 0, 0, 0.4);
        }
      }
      .vux-uploader_file-content {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        color: #fff;
        .upload-error {
          display: inline-block;
          font-size: 23px;
          color: #f43530;
          font-family: 'weui';
          font-style: normal;
          &:before {
            content: '\EA0B';
          }
        }
      }
    }
    .vux-uploader_input-box {
      float: left;
      position: relative;
      margin-left: 9px;
      margin-bottom: 9px;
      width: 77px;
      height: 77px;
      border: 1px solid #d9d9d9;
      &:before,
      &:after {
        content: ' ';
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        background-color: #d9d9d9;
      }
      &:before {
        width: 2px;
        height: 39.5px;
      }
      &:after {
        width: 39.5px;
        height: 2px;
      }
      .vux-uploader_input {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      }
    }
  }
}
</style>



